import { Button, Checkbox, Input, makeStyles, Select, tokens, useId } from "@fluentui/react-components";
import * as React from "react";
import { useTranslation } from "react-i18next";

import type { CheckboxProps, InputProps } from "@fluentui/react-components";
import type { CalendarStrings } from "@fluentui/react-datepicker-compat";
import { DatePicker, defaultDatePickerStrings } from "@fluentui/react-datepicker-compat";

const zhCNLocalizedStrings = {
  months: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
  shortMonths: ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],
  days: ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"],
  shortDays: ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],
};

const localizedStrings: CalendarStrings = { ...zhCNLocalizedStrings, goToToday: "今天" };

export interface FormItemListProps {}

const useStyles = makeStyles({
  container__main: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",

    "> fieldset": {
      width: "270px",
    },
  },
  message: {
    fontSize: tokens.fontSizeBase500,
    fontColor: tokens.colorNeutralBackgroundStatic,
    fontWeight: tokens.fontWeightRegular,
    paddingLeft: "10px",
    paddingRight: "10px",
  },

  control: {
    width: "160px",
  },
  control1: {
    width: "266px",
  },
});

const FormItemList: React.FC<FormItemListProps | undefined> = () => {
  const { t, i18n } = useTranslation();

  const styles = useStyles();

  const readingSelectId = useId("reading-selectId");
  const beforeAndAfterId = useId("content-before-and-after");
  const unreadEmailSelectId = useId("unread-email-selectId");
  const [readValue, setReadValue] = React.useState<string>("true");
  const [unreadValue, setUnreadValue] = React.useState<string>("true");
  const [readCountValue, setReadCountValue] = React.useState<string>();
  const [selectedDate, setSelectedDate] = React.useState<Date | null | undefined>(null);
  const [vistChecked, setVisitChecked] = React.useState<CheckboxProps["checked"]>(true);
  const [readCountChecked, setReadCountChecked] = React.useState<CheckboxProps["checked"]>(true);

  // @ts-ignore
  const onInputChange: InputProps["onChange"] = (evt, data) => {
    if (!data.value) {
      setReadCountChecked(true);
    }

    // 数值限制输入20位
    if (data.value.length <= 20) {
      setReadCountValue(data.value);
      setReadCountChecked(false);
    }
  };

  const onFormatDate = (date?: Date) => {
    if (!date) {
      setVisitChecked(true);
      return "";
    } else {
      setVisitChecked(false);
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
    }
  };

  // @ts-ignore
  const handleOnChange = (type: string) => (evt, data) => {
    switch (type) {
      case "count":
        setReadCountChecked(data.checked);
        break;
      case "visit":
        setVisitChecked(data.checked);
        break;
      case "unread":
        setUnreadValue(data.value);
        break;
      case "read":
        setReadValue(data.value);
        break;
      default:
        break;
    }
  };

  // 阅读次数、访问有效期 未填写时，默认为无限制
  const onSave = () => {
    const params = {
      strategyTotalReadCount: readCountValue,
      strategyUnreadRemind: Boolean(unreadValue),
      strategyReadRemind: Boolean(readValue),
      strategyExpireDate: selectedDate?.toISOString(),
    };
    if (readCountChecked) {
      delete params.strategyTotalReadCount;
    }

    if (vistChecked) {
      delete params.strategyExpireDate;
    }
    console.log("params", params);
    window.sessionStorage.setItem("strategy", JSON.stringify(params));
  };

  return (
    <div className={styles.container__main}>
      <fieldset>
        <legend>{t("加密模版")}</legend>
        <label>{t("不可转发")}</label>
      </fieldset>

      <fieldset>
        <legend>{t("阅读次数")}</legend>
        <Checkbox checked={readCountChecked} onChange={handleOnChange("count")} label={t("无限制")} />
        <Input
          id={beforeAndAfterId}
          value={readCountValue}
          onChange={onInputChange}
          type="number"
          maxLength={20}
          className={styles.control}
        />
      </fieldset>

      <fieldset>
        <legend>{t("访问有效期")}</legend>
        <Checkbox label={t("无限制")} checked={vistChecked} onChange={handleOnChange("visit")} />

        <DatePicker
          strings={i18n.language === "zh" ? localizedStrings : defaultDatePickerStrings}
          placeholder={t("请选择")}
          value={selectedDate}
          onSelectDate={setSelectedDate}
          formatDate={onFormatDate}
          className={styles.control}
        />
      </fieldset>

      <fieldset>
        <legend>{t("未读邮件提醒")}</legend>
        <Select
          id={unreadEmailSelectId}
          value={unreadValue}
          className={styles.control1}
          aria-placeholder={t("请选择")}
          onChange={handleOnChange("unread")}
        >
          <option value="true">{t("提醒收件人")}</option>
          <option value="false">{t("不提醒")}</option>
        </Select>
      </fieldset>

      <fieldset>
        <legend>{t("邮件阅读提醒")}</legend>
        <Select
          id={readingSelectId}
          value={readValue}
          className={styles.control1}
          aria-placeholder={t("请选择")}
          onChange={handleOnChange("read")}
        >
          <option value="true">{t("提醒发件人")}</option>
          <option value="false">{t("不提醒")}</option>
        </Select>
      </fieldset>

      <br />
      <Button appearance="primary" onClick={onSave} disabled={false}>
        {t("保存")}
      </Button>
    </div>
  );
};

export default FormItemList;
